import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';

dayjs.extend(relativeTime)

require('dayjs/locale/zh-cn')
dayjs.locale('zh-cn')

export const FORMAT_DATE_TIME_TEMPLATE = 'YYYY-MM-DD HH:mm:ss';
export const FORMAT_DATE_TEMPLATE = 'YYYY-MM-DD';
export const FORMAT_TIME_TEMPLATE = 'HH:mm:ss';

export const formatDateTime = (date?: dayjs.ConfigType, template?: string) => {
  if (!template) template = FORMAT_DATE_TIME_TEMPLATE;
  return dayjs(date).format(template);
};

export const formatDate = (date?: dayjs.ConfigType, template?: string) => {
  if (!template) template = FORMAT_DATE_TEMPLATE;
  return dayjs(date).format(template);
};

export const formatTime = (date?: dayjs.ConfigType, template?: string) => {
  if (!template) template = FORMAT_TIME_TEMPLATE;
  return dayjs(date).format(template);
};

export const fromNow = (date?: dayjs.ConfigType, template?: string) => {
  if (!template) template = FORMAT_TIME_TEMPLATE;
  return dayjs(date).fromNow();
};
