import { AxiosResponse } from "axios";
import { BgArchive, BgArchiveType } from "./index.d";
import request from "../../..";

export const queryArchiveList = () => {
    return request.get<null, AxiosResponse<BgArchive[]>>('/bg-archive-list.json');
}

export const queryArchive = (uuid: string) => {
    return request.get<null, AxiosResponse<BgArchive>>(`/archive/${uuid}.json`);
}

export const queryArchiveTypeList = () => {
    return request.get<null, AxiosResponse<BgArchiveType[]>>('/bg-archive-type.json');
}