import { createSlice } from "@reduxjs/toolkit";

export const BG_CONFIG_SLICE_NAME = 'bgConfig';

export const bgConfigSlice = createSlice({
  name: BG_CONFIG_SLICE_NAME,
  initialState: {
    pageTitle: '',
    headTitle: '',
    copyright: '',
  },
  reducers: {
    setState: (state, action) => {
        state.pageTitle = action.payload.pageTitle;
        state.headTitle = action.payload.headTitle;
        state.copyright = action.payload.copyright;
    },
  },
});

export const { setState } = bgConfigSlice.actions;

export default bgConfigSlice.reducer;