import React, { useEffect, useState } from 'react';
import styles from './index.module.scss'
import { queryArchiveList } from '../../request/api/blog/archive';
import { BgArchive } from '../../request/api/blog/archive/index.d';
import { useNavigate } from 'react-router-dom';
import { fromNow } from '../../utils/dateTimeUtils';

export const ArchivesList = () => {
  
  const navigate = useNavigate();
  const [list, setList] = useState<BgArchive[]>([]);

  const query = () => {
    queryArchiveList().then(res => {
      console.debug('', res);
      if (Array.isArray(res.data)) {
        const resultList = res.data.sort((i1, i2) => {return Number(i2.createDate) - Number(i1.createDate)});
        setList(resultList);
      }
    })
  }

  useEffect(() => {
    query();
  }, []);
  
  return (
    <div className={styles.index}>
      <div className='archive-list'>
        {list?.length > 0 && list.map(archive => {
          return (
            <div className='archive-item' key={archive.uuid}>
              <div className='title'>
                <a href="javascript:void(0);" onClick={() => {
                  navigate(`/archives/${archive.uuid}`)
                }}>{archive.title}</a>
                
              </div>
              <div className='content'>{archive.content}</div>
              <div className='time'>{fromNow(Number(archive.createDate))}</div>
            </div>
          )
        })}
        

      </div>

    </div>
  );
}

export default ArchivesList;
